import Container from "components/auth/mypage/bookmark/Container";
import Part from "components/auth/mypage/basic/Part";
import { useEffect, useState } from "react";
import { getUserBookmark } from "controllers/bookmark";
import Wrapper from "components/auth/mypage/bookmark/Wrapper";
import {Label} from "components/auth/mypage/bookmark/Set";
import Card from "components/company/Card";
import Information from "components/company/Information";
import Title from "components/company/Title";

const Bookmark = () => {

  const [result, setResult] = useState<any|any[]>([]);
  
  useEffect(() => {
    getMarkedBook();
  }, []);

  const getMarkedBook = async() => {
    const res = await getUserBookmark();
    setResult(res[0].bookmark);
    // console.log(res[0].bookmark);
  }
  
  return (
    <>
      <Part>관심업체</Part>
      <Container>
        {
          result && result.map((res:any, idx:number) => (
            <Wrapper key={res._id}>
              <Card>
                <Title>{res.title}</Title>
                <Information>{res.address}</Information>
                <Information>{res.telephone}</Information>
                <Information>{res.open[0]}</Information>
                <Information>{res.tag.slice(0,4).map((res:any, idx:number) => (<span key={idx}> #{res}</span>))}</Information>
              </Card>
              <Label src="/images/icons/Label.png" alt="즐겨찾기" />
            </Wrapper>
          ))
        }
      </Container>
    </>
  )
}

export default Bookmark;